import { Component, OnInit } from '@angular/core'
import { AssetMaintenanceCodeService } from 'src/app/sdk/asset/services'
import { Maintenance_code } from 'src/app/sdk/asset/models'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) {}

    data: Maintenance_code

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Maintenance_code>
    form() {
        this.dataForm = this.fb.group<Maintenance_code>({
            code: new FormControl('', [Validators.required]),
            work: new FormControl(''),
            description: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.assetMaintenanceCodeService.postAssetMaintenanceCodes(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/master/maintenance-code/list'])
    }
}
