import { ActivatedRoute, Router } from '@angular/router'
import { Component, OnInit } from '@angular/core'
import { AssetMaintenanceCodeService } from 'src/app/sdk/asset/services'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Maintenance_code } from 'src/app/sdk/asset/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    data: Maintenance_code
    id_maintenance_code: string = ''

    ngOnInit() {
        this.id_maintenance_code = this.activatedRoute.snapshot.paramMap.get('id_maintenance_code')
        this.form()
        this.getData()
    }

    getData() {
        this.assetMaintenanceCodeService
            .getAssetMaintenanceCodesId(this.id_maintenance_code)
            .subscribe(data => {
                this.dataForm.setValue(data.data)
            })
    }

    dataForm: FormGroupTypeSafe<Maintenance_code>
    form() {
        this.dataForm = this.fb.group<Maintenance_code>({
            code: new FormControl('', [Validators.required]),
            work: new FormControl(''),
            description: new FormControl(''),
            id_user: new FormControl(''),
            id_maintenance_code: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.assetMaintenanceCodeService
            .putAssetMaintenanceCodesId({ id: this.id_maintenance_code, body: this.data })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/asset/master/maintenance-code/list'])
    }
}
