import { Component, OnInit } from '@angular/core'
import { AssetMaintenanceCodeService } from 'src/app/sdk/asset/services/asset-maintenance-code.service'
import { Maintenance_code } from 'src/app/sdk/asset/models/maintenance-_code'
import { FilterFuctionService } from 'src/app/services/filter-fuction.service'
import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
    constructor(
        private assetMaintenanceCodeService: AssetMaintenanceCodeService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) {}

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Maintenance_code[] = []
    listOfData: Maintenance_code[] = []
    getData() {
        this.spinner.show()
        this.assetMaintenanceCodeService.getAssetMaintenanceCodes(JSON.stringify({sortKey: this.sortKey, sortValue:this.sortValue, search: this.search})).subscribe(data => {
            this.listOfDisplayData = data.data
            this.listOfData = data.data
            this.spinner.hide()
        })
    }


    sortKey: string | null = null
    sortValue: string | null = null
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }
    search: string = ''

    delete(id) {
        this.spinner.show()
        this.assetMaintenanceCodeService.deleteAssetMaintenanceCodesId(id).subscribe(data => {
            this.getData()
            this.spinner.hide()
        })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }
}
